General Layout body {
  font-family: "Arial", sans-serif;
  background-color: #f4f7fc;
  margin: 0;
  padding: 0;

  height: 100vh;
}

.login-container {
  background-color: #ffffff;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 50%;
  max-width: 500px;
  text-align: center;
  margin: 50px auto;
}

.login-form h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.input-group {
  margin-bottom: 15px;
}

.input-group label {
  font-size: 14px;
  color: #555;
}

.input-group input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
  color: #333;
}

.input-group input:focus {
  border-color: #4caf50;
  outline: none;
}

