.login-btn {
  background-color: #4caf50;
  color: white;
  margin-bottom: 15px;
}

.login-btn:hover {
  background-color: #45a049;
}

.divider {
  text-align: center;
  margin: 10px 0;
  color: #888;
}

.google-btn {
  background-color: #db4437;
  color: white;
  font-size: 16px;
}

.google-btn:hover {
  background-color: #c1351d;
}
